import MockImage from '#components/Image';

import { Product, ProductSpec } from '../types';

export class ProductBuilder {
  product = {
    anchorHref: '',
    anchorId: '',
    bestForPrefixed: '',
    id: '',
    prettyPrice: '',
    rating: 0,
    title: '',
    vendorLink: '',
    image: <MockImage />,
    endpoint: '',
    isExternal: true,
    isAvailable: true,
    bestFor: '',
    productSpec: [] as ProductSpec[],
  } as Product;

  anchorHref(anchorHref: string) {
    this.product.anchorHref = anchorHref;

    return this;
  }

  bestFor(bestFor: string) {
    this.product.bestFor = bestFor;

    return this;
  }

  bestForPrefixed(bestForPrefixed: string) {
    this.product.bestForPrefixed = bestForPrefixed;

    return this;
  }

  endpoint(endpoint: string) {
    this.product.endpoint = endpoint;

    return this;
  }

  anchorId(anchorId: string) {
    this.product.anchorId = anchorId;

    return this;
  }

  isExternal(isExternal: boolean) {
    this.product.isExternal = isExternal;

    return this;
  }

  isAvailable(isAvailable: boolean) {
    this.product.isAvailable = isAvailable;

    return this;
  }

  id(id: string) {
    this.product.id = id;

    return this;
  }

  image(image: ReactNode) {
    this.product.image = image;

    return this;
  }

  prettyPrice(prettyPrice: string) {
    this.product.prettyPrice = prettyPrice;

    return this;
  }

  productSpec(productSpec: ProductSpec[]) {
    this.product.productSpec = productSpec;

    return this;
  }

  vendorLink(vendorLink: string) {
    this.product.vendorLink = vendorLink;

    return this;
  }

  rating(rating: number) {
    this.product.rating = rating;

    return this;
  }

  title(title: string) {
    this.product.title = title;

    return this;
  }

  build() {
    return this.product;
  }
}
